import {FormControl, Grid, InputLabel, MenuItem, Select, Stack, TextField, TextFieldProps} from '@mui/material';
import dayjs, { Dayjs } from 'dayjs';
import { useMemo, useState } from 'react';
import { DesktopDatePicker, LocalizationProvider } from '@mui/x-date-pickers';

import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import MonthlyFateChart from './MothlyFateChart';
import {IGraphProps} from "../Layout.contract";

const MonthlyFate = ({birthDate, setBirthDate}: IGraphProps) => {
  const [months] = useState<number>(12);
  const [year, setYear] = useState<Dayjs>(dayjs());
  const [startingMonth, setStartingMonth] = useState<number>(0);
  const [monthOptions] = useState(
      dayjs.months().map((monthName, index) => ({id: index, title: monthName}))
  );

  const handleYearChange = (value: Dayjs | null) => {
    if (!value) {
      setYear(dayjs());
    }

    setYear(value!);
  };

  const dayMonthValue = useMemo(() => {
    if (!birthDate) {
      return '';
    }

    return `${birthDate?.format('D')}${birthDate?.format('MM')}`;
  }, [birthDate]);

  const chartNumbers = useMemo(() => {
    if (!dayMonthValue || !birthDate || !months || !year) {
      return '';
    }

    if (startingMonth > 0) {
      let firstYear = (
          parseInt(dayMonthValue, 10) *
          birthDate.year() *
          months *
          year.year()
      ).toString();

      if (firstYear.length < 12) {
        firstYear += firstYear[0]
      }

      let secondYear = (
          parseInt(dayMonthValue, 10) *
          birthDate.year() *
          months *
          year.year() + 1
      ).toString();

      return firstYear.substring(startingMonth, 12) + secondYear.substring(0, startingMonth)
    }

    let nums = (
        parseInt(dayMonthValue, 10) *
        birthDate.year() *
        months *
        year.year()
    ).toString();

    if (nums.length < 12) {
      nums += nums[0];
    }

    return nums;
  }, [dayMonthValue, birthDate, months, year, startingMonth]);

  return (
    <Grid container spacing={2}>
      <Grid item xs={12} md={3}>
        <LocalizationProvider dateAdapter={AdapterDayjs} adapterLocale="ru">
          <Grid container spacing={2} alignItems="center" justifyItems="center" direction="column">
            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <DesktopDatePicker
                  label=" "
                  inputFormat="DD.MM.YYYY"
                  value={birthDate}
                  onChange={setBirthDate}
                  renderInput={(
                      params: JSX.IntrinsicAttributes & TextFieldProps
                  ) => <TextField {...params} fullWidth />}
              />
            </Grid>
            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <DesktopDatePicker
                  label=" "
                  inputFormat="YYYY"
                  views={['year']}
                  value={year}
                  onChange={handleYearChange}
                  renderInput={(
                      params: JSX.IntrinsicAttributes & TextFieldProps
                  ) => <TextField {...params} fullWidth />}
              />
            </Grid>
            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <FormControl fullWidth>
                <InputLabel> </InputLabel>
                <Select
                    value={startingMonth}
                    label=" "
                    onChange={(e) => setStartingMonth(Number(e.target.value))}
                >
                  {monthOptions.map((option) => <MenuItem key={option.id} value={option.id}>{option.title}</MenuItem>)}
                </Select>
              </FormControl>
            </Grid>
            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <TextField
                  label="/"
                  disabled
                  aria-readonly
                  value={dayMonthValue}
                  fullWidth
              />
            </Grid>

            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <TextField
                  label=" "
                  type="number"
                  disabled
                  value={months}
                  fullWidth
              />
            </Grid>

            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <TextField
                  label=" "
                  type="text"
                  disabled
                  value={chartNumbers}
                  fullWidth
              />
            </Grid>
          </Grid>
        </LocalizationProvider>
      </Grid>
      <Grid item xs={12} md={6}>
        <MonthlyFateChart startingMonth={startingMonth} chartNumbers={chartNumbers} />
      </Grid>
    </Grid>
  );
};

export default MonthlyFate;
