import dayjs from 'dayjs';
import { useMemo } from 'react';
import { IMonthlyFateChartProps } from './MonthlyFate.contract';
import ChartGraph from '../ChartGraph/ChartGraph';

const MonthlyFateChart = ({ chartNumbers, startingMonth }: IMonthlyFateChartProps) => {
  const firstYearMonths = dayjs.monthsShort();
  const secondYearMonths = dayjs.monthsShort();

  const months = [...firstYearMonths.slice(startingMonth, 12), ...secondYearMonths.slice(0, startingMonth)];


  const perMonthArray = useMemo<number[]>(() => {
    const res = chartNumbers.split('').map((val) => parseInt(val, 10));

    return res;
  }, [chartNumbers]);

  return <ChartGraph imageName={'  '} defaultColors={['#a5a5a5']} data={[perMonthArray]} labels={months} />;
};

export default MonthlyFateChart;
