import {
  CategoryScale,
  Chart as ChartJS,
  Filler,
  Legend,
  LinearScale,
  LineElement,
  PointElement,
  Title,
  Tooltip,
} from 'chart.js';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import {getMonthNums, getYearsNums, yearFractionToString} from "./date";
import {Point} from "line-intersect";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  ChartDataLabels,
  Filler,
);

export const CanvasColor = {
  id: 'customCanvasBackgroundColor',
  beforeDraw: (chart: ChartJS, args: any, options: any) => {
    const {ctx} = chart;
    ctx.save();
    ctx.globalCompositeOperation = 'destination-over';
    ctx.fillStyle = options.color || '#ffffff';
    ctx.fillRect(0, 0, chart.width, chart.height);
    ctx.restore();
  },

};

export const WillFateAgePlugin = {
  id: 'willFateAgeLine',
  afterDraw: (chart: ChartJS, args: any, options: any) => {
    if (!options.age?.years) {
      return;
    }

    const {ctx} = chart;

    ctx.setLineDash([5, 3]);
    ctx.beginPath();

    const leftOffset = 37;
    const sectionPx = 7.7;
    ctx.moveTo(leftOffset + sectionPx * options.age.years, 40);
    ctx.lineTo(leftOffset + sectionPx * options.age.years, chart.height - 30);
    ctx.stroke();

    ctx.fillStyle = '#696969FF'
    ctx.fillText(` : ${getYearsNums(options.age.years)} ${getMonthNums(options.age.months)}`, chart.width - 350, 26);
  }
}

export const WillFateIntersectionPlugin = {
  id: 'willFateIntersections',
  beforeDraw: (chart: ChartJS, args: any, options: any) => {
    if (!options.intersections) {
      return;
    }

    const {ctx} = chart;
    const {intersections} = options;

    ctx.fillStyle = '#696969FF'
    if (options.intersections.length < 1) {
      ctx.fillText('   ',  chart.width - 350, 52);
    } else {
      ctx.fillText('     :',  chart.width - 350, 52);
    }

    const filledIntersections = new Set();
    intersections.forEach((point: Point, index: number) => {
      const str = yearFractionToString(point.x);

      if (!filledIntersections.has(str)) {
        ctx.fillText(str, chart.width - 350,  78 + filledIntersections.size * 26);
        filledIntersections.add(str);
      }
    });
  },
};