import { useMemo, useState } from 'react';
import dayjs, { Dayjs } from 'dayjs';
import { Grid, Stack, TextField, TextFieldProps } from '@mui/material';
import { DesktopDatePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import WillFateGraph from './WillFateGraph';
import {IGraphProps} from "../Layout.contract";

const WillFate = ({birthDate, setBirthDate}: IGraphProps) => {
  const dayMonthValue = useMemo(() => {
    if (!birthDate) {
      return '';
    }

    return `${birthDate?.format('DD')}${birthDate?.format('MM')}`;
  }, [birthDate]);

  const fateChartNumbers = useMemo(() => {
    if (!dayMonthValue || !birthDate) {
      return '';
    }

    let res = (parseInt(dayMonthValue, 10) * birthDate.year()).toString();

    if (res.length < 7) {
      res = `0${res}`;
    }

    return res;
  }, [dayMonthValue, birthDate]);

  const willChartNumbers = useMemo(() => {
    if (!dayMonthValue || !birthDate) {
      return '';
    }

    let res = (
      parseInt(dayMonthValue.replaceAll('0', '1'), 10) *
      parseInt(birthDate.year().toString().replaceAll('0', '1'), 10)
    ).toString();

    if (res.length < 7) {
      res = `0${res}`;
    }

    return res;
  }, [dayMonthValue, birthDate]);

  const age = useMemo(() => {
    const currentDate = dayjs();

    const years = currentDate.diff(birthDate, 'y');
    const months = currentDate.diff(birthDate, 'months') % 12;

    return {
      years,
      months,
    }
  }, [birthDate])

  return (
    <Grid container spacing={2}>
      <Grid item xs={12} md={3}>
        <LocalizationProvider dateAdapter={AdapterDayjs} adapterLocale="ru">
          <Grid container spacing={2} alignItems="center" justifyItems="center" direction="column">
            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <DesktopDatePicker
                label=" "
                inputFormat="DD.MM.YYYY"
                value={birthDate}
                onChange={setBirthDate}
                renderInput={(
                  params: JSX.IntrinsicAttributes & TextFieldProps
                ) => <TextField {...params} fullWidth />}
              />
            </Grid>
            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <TextField
                label="  "
                type="text"
                disabled
                value={fateChartNumbers}
                fullWidth
              />
            </Grid>
            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <TextField
                label="  "
                type="text"
                disabled
                value={willChartNumbers}
                fullWidth
              />
            </Grid>
          </Grid>
        </LocalizationProvider>
      </Grid>
      <Grid item xs={12} md={8}>
        <WillFateGraph
          willChartNumbers={willChartNumbers}
          fateChartNumbers={fateChartNumbers}
          age={age}
        />
      </Grid>
    </Grid>
  );
};

export default WillFate;
