import {useMemo, useState} from 'react';
import dayjs, {Dayjs} from 'dayjs';
import {Grid, TextField, TextFieldProps} from '@mui/material';
import {DesktopDatePicker, LocalizationProvider} from '@mui/x-date-pickers';
import {AdapterDayjs} from '@mui/x-date-pickers/AdapterDayjs';
import YearlyFateChart from './YearlyFateChart';
import {IGraphProps} from "../Layout.contract";

const YearlyFate = ({birthDate, setBirthDate}: IGraphProps) => {
  const [startYear, setStartYear] = useState<Dayjs | null>(dayjs());
  const [endYear, setEndYear] = useState<Dayjs | null>(dayjs('2030'));

  const dayMonthValue = useMemo(() => {
    if (!birthDate) {
      return '';
    }

    return `${birthDate?.format('D')}${birthDate?.format('MM')}`;
  }, [birthDate]);

  const chartNumbers = useMemo(() => {
    if (!dayMonthValue || !birthDate) {
      return '';
    }

    return (parseInt(dayMonthValue, 10) * birthDate.year()).toString();
  }, [dayMonthValue, birthDate]);

  return (
    <Grid container spacing={2}>
      <Grid item xs={12} md={3}>
        <LocalizationProvider dateAdapter={AdapterDayjs} adapterLocale="ru">
          <Grid container spacing={2} alignItems="center" justifyItems="center" direction="column">
            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <DesktopDatePicker
                label=" "
                inputFormat="DD.MM.YYYY"
                value={birthDate}
                onChange={setBirthDate}
                renderInput={(
                  params: JSX.IntrinsicAttributes & TextFieldProps
                ) => <TextField {...params} fullWidth />}
              />
            </Grid>
            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <DesktopDatePicker
                label=" "
                inputFormat="YYYY"
                value={startYear}
                views={['year']}
                maxDate={endYear!}
                onChange={setStartYear}
                renderInput={(
                  params: JSX.IntrinsicAttributes & TextFieldProps
                ) => <TextField {...params} fullWidth />}
              />
            </Grid>
            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <DesktopDatePicker
                label=" "
                inputFormat="YYYY"
                value={endYear}
                views={['year']}
                minDate={startYear!}
                onChange={setEndYear}
                renderInput={(
                  params: JSX.IntrinsicAttributes & TextFieldProps
                ) => <TextField {...params} fullWidth />}
              />
            </Grid>
            <Grid item container spacing={-5} xs={12} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
              <TextField
                label=" "
                type="text"
                disabled
                value={chartNumbers}
                fullWidth
              />
            </Grid>
          </Grid>
        </LocalizationProvider>
      </Grid>
      <Grid item md={6}>
        <YearlyFateChart
          startYear={startYear?.year()!}
          endYear={endYear?.year()!}
          chartNumbers={chartNumbers}
          birthYear={birthDate?.year()!}
        />
      </Grid>
    </Grid>
  );
};

export default YearlyFate;
