import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Box, Grid,
  Slider,
  Stack,
  Typography,
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { SliderPicker } from '@hello-pangea/color-picker';
import { IChartSettingsProps } from './ChartGraph.contract';

const ChartSettings = ({
  index,
  color,
  changeColor,
  changeTension,
}: IChartSettingsProps) => {
  return (
    <Accordion>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls={`panel${index}-content`}
        id={`panel${index}-header`}
      >
        <Typography>  {index + 1}</Typography>
      </AccordionSummary>
      <AccordionDetails>
        <Grid container spacing={2} direction="column">
          <Grid item xs={12}>
            <Typography> :</Typography>
            <Slider
              min={0}
              max={1}
              step={0.05}
              defaultValue={0}
              onChange={(e, v) => changeTension(v as number, index)}
            />
          </Grid>
          <Grid item xs={12}>
            <Typography> :</Typography>
            <SliderPicker
              color={color}
              onChangeComplete={(c) => changeColor(c.hex, index)}
            />
          </Grid>
        </Grid>
      </AccordionDetails>
    </Accordion>
  );
};

export default ChartSettings;
