import {useEffect, useMemo, useRef, useState} from 'react';
import { ChartJSOrUndefined } from 'react-chartjs-2/dist/types';
import {Box, Button, Grid, Paper, Stack} from '@mui/material';
import { Line } from 'react-chartjs-2';
import SaveIcon from '@mui/icons-material/Save';
import { saveCanvasAsImage } from '../../common/file';
import { IChartGraphProps } from './ChartGraph.contract';
import ChartSettings from './ChartSettings';
import './ChartGraph.css'
import {CanvasColor, WillFateIntersectionPlugin, WillFateAgePlugin} from "../../common/chartjs";

const ChartGraph = ({
  data,
  labels,
  legends = [],
  defaultColors,
  displayNumbers = true,
  xScaleTitle,
  imageName,
  opts,
  contentSpace
}: IChartGraphProps) => {
  const chartRef = useRef<ChartJSOrUndefined>(null);
  const [tensions, setTensions] = useState<number[]>(data.map(() => 0));
  const [colors, setColors] = useState<string[]>(defaultColors);

  const datasets = useMemo(() => {
    return data.map((dataset, index) => ({
      data: dataset,
      fill: false,
      backgroundColor: colors[index],
      pointBackgroundColor: '#474747',
      borderColor: colors[index],
      tension: tensions[index],
      label: legends[index] || '',
    }));
  }, [tensions, data, colors]);

  const dataProp = {
    labels,
    datasets,
  };

  const options = {
    responsive: false,
    scales: {
      x: {
        title: {
          display: Boolean(xScaleTitle),
          text: xScaleTitle
        },
      },
      y: {
        suggestedMin: 0,
        suggestedMax: 10,
      },
    },
    layout: {
      padding: {
        top: 10,
        left: 10,
        right: contentSpace ? 400 : 10,
      },
    },
    plugins: {
      legend: {
        display: legends?.length,
        position: 'top' as const,
      },
      datalabels: {
        display: displayNumbers,
        anchor: 'end',
        align: 'end',
        font: {
          weight: 'normal',
          size: 13,
        },
      },
      customCanvasBackgroundColor: {
        color: 'white',
      },
      ...opts,
    },
  };

  const changeColor = (newColor: string, index: number) => {
    const newColors = colors.map((c, i) => {
      if (i === index) {
        return newColor;
      }

      return c;
    });

    setColors(newColors);
  };

  const changeTension = (newTension: number, index: number) => {
    const newTensions = tensions.map((t, i) => {
      if (i === index) {
        return newTension;
      }

      return t;
    });

    setTensions(newTensions);
  };

  const onSaveClick = () => {
    if (!chartRef) {
      return;
    }

    saveCanvasAsImage(
      chartRef?.current?.canvas as HTMLCanvasElement,
      imageName ?? ''
    );
  };

  useEffect(() => {
    if (!chartRef) {
      return;
    }

    // @ts-ignore
    const canvas = chartRef.current.canvas;

    const ctx = canvas.getContext("2d")!;
    ctx.setLineDash([5, 3]);
    ctx.beginPath();
    ctx.moveTo(0,100);
    ctx.lineTo(400, 100);
    ctx.stroke();
  }, [dataProp]);

  return (
    <Stack spacing={2}>
      <Box>
        <Paper elevation={1} className="chartGraph">
          <Box>
            <Line
              width={contentSpace ? 1000 : 600}
              height={400}
              /* @ts-ignore */
              ref={chartRef}
              /* @ts-ignore */
              options={options}
              data={dataProp}
              plugins={[CanvasColor, WillFateAgePlugin, WillFateIntersectionPlugin]}
            />
          </Box>

        </Paper>
      </Box>
      <Grid>
        {data.map((dataset, index) => (
          <ChartSettings
            index={index}
            color={colors[index]}
            tension={tensions[index]}
            changeColor={changeColor}
            changeTension={changeTension}
          />
        ))}
      </Grid>
      <Grid container spacing={2} alignItems="center" justifyItems="center" direction="column">
        <Button
          variant="contained"
          startIcon={<SaveIcon />}
          onClick={onSaveClick}
          title=""
        >
          
        </Button>
      </Grid>
    </Stack>
  );
};

export default ChartGraph;
