import { PropsWithChildren, useMemo, useState } from 'react';
import {Box, Grid, Paper, Tab, Tabs, TextField} from '@mui/material';
import { ITabPanelProps, TabNum, TabType } from './Layout.contract';
import MonthlyFate from './MonthlyFate/MonthlyFate';
import YearlyFate from './YearlyFate/YearlyFate';
import WillFate from './WillFate/WillFate';
import dayjs, {Dayjs} from "dayjs";

const TabPanel = ({
  value,
  index,
  children,
}: PropsWithChildren<ITabPanelProps>) => {
  const isVisible = useMemo(() => value === index, [value, index]);

  return <Box hidden={!isVisible}>{children}</Box>;
};


/*
     ,       
 */
const Layout = () => {
  const [currentTab, setCurrentTab] = useState(TabNum.Monthly);
  const [nsp, setNsp] = useState('');
  const [birthDate, setBirthDate] = useState<Dayjs | null>(null);

  const tabChange = (event: React.SyntheticEvent, newTab: number) => {
    setCurrentTab(newTab);
  };

  if (nsp !== 'reply2022') {
    return <Grid container spacing={2} justifyContent="center" direction="row" alignItems="center" justifyItems="center">
      <Grid item container md={6}  justifyContent="center" direction="row" alignItems="center" justifyItems="center">
        <Paper>
          <TextField label="" type="password" value={nsp} onChange={(e) => setNsp(e.target.value)} />
        </Paper>
      </Grid>
    </Grid>
  }

  return (
      <div>
        <Grid container spacing={2}>
          <Grid item md={12}>
            <Tabs value={currentTab} onChange={tabChange}>
              <Tab label={TabType[TabNum.Monthly]} />
              <Tab label={TabType[TabNum.Yearly]} />
              <Tab label={TabType[TabNum.Fate]} />
            </Tabs>
          </Grid>
          <Grid item md={12}>
            <TabPanel index={TabNum.Monthly} value={currentTab}>
              <MonthlyFate birthDate={birthDate} setBirthDate={setBirthDate} />
            </TabPanel>
            <TabPanel index={TabNum.Yearly} value={currentTab}>
              <YearlyFate birthDate={birthDate} setBirthDate={setBirthDate} />
            </TabPanel>
            <TabPanel index={TabNum.Fate} value={currentTab}>
              <WillFate birthDate={birthDate} setBirthDate={setBirthDate} />
            </TabPanel>
          </Grid>
        </Grid>
      </div>
  );
};

export default Layout;
