import { useMemo } from 'react';
import ChartGraph from '../ChartGraph/ChartGraph';
import { IYearlyFateProps } from './YearlyFate.contract';
import {calculateYearShift, makeRepeated} from "../../common/date";

const YearlyFateChart = ({
  chartNumbers,
  startYear,
  endYear,
  birthYear,
}: IYearlyFateProps) => {
  const labels = endYear > startYear ? Array(endYear - startYear + 1)
    .fill(0)
    .map((d, i) => (i + startYear).toString()) : [];

  const perYearArray = useMemo<number[]>(() => {
    try {
      const yearShift = calculateYearShift(
          birthYear,
          startYear,
          chartNumbers.length
      );

      const arr = makeRepeated(
          chartNumbers.split('').map((val) => parseInt(val, 10)),
          10
      );

      arr.splice(0, yearShift);

      return arr;
    }
    catch (e) {
      return [];
    }

  }, [birthYear, chartNumbers, startYear]);

  return <ChartGraph imageName={'  '} defaultColors={['#4472c4']} data={[perYearArray]} labels={labels} />;
};

export default YearlyFateChart;
