import {useCallback, useMemo} from 'react';
import { IWillFateGraphProps } from './WillFate.contract';
import ChartGraph from '../ChartGraph/ChartGraph';
import {checkIntersection, IntersectionCheckResult} from "line-intersect";



const WillFateGraph = ({
  willChartNumbers,
  fateChartNumbers,
  age
}: IWillFateGraphProps) => {
  const labels = ['0', '12', '24', '36', '48', '60', '72'];

  const fateArray = useMemo<number[]>(() => {
    return fateChartNumbers.split('').map((val) => parseInt(val, 10));
  }, [fateChartNumbers]);

  const willArray = useMemo<number[]>(() => {
    return willChartNumbers.split('').map((val) => parseInt(val, 10));
  }, [willChartNumbers]);

  const intersections = useMemo(() => {
    const _intersections = [];

    for (let i = 0; i < fateArray.length - 1; i++) {
      const currPoint = i * 12;
      const nextPoint = (i + 1) * 12;

      const intersection: IntersectionCheckResult = checkIntersection(
        currPoint, fateArray[i], nextPoint, fateArray[i + 1],
        currPoint, willArray[i], nextPoint, willArray[i + 1],
      );

      if (intersection.type === 'intersecting') {
        // @ts-ignore
        _intersections.push(intersection?.point);
      }
    }

    return _intersections;
  }, [willArray, fateArray]);

  return (
    <ChartGraph
      defaultColors={['#a4bae1', '#daa681']}
      data={[fateArray, willArray]}
      labels={labels}
      legends={[' ', ' ']}
      xScaleTitle={' '}
      imageName={'   '}
      displayNumbers={false}
      opts={{
        willFateAgeLine: {
          age: age
        },
        willFateIntersections: {
          intersections,
        }
      }}
      contentSpace={true}
    />
  );
};

export default WillFateGraph;
